#main {
    flex: 1 1 auto;
    display: flex;
}

#home {
    flex: 1 1 auto;
    display: flex;
    flex-direction: row;
}

#canvasContainer {
    flex: 1 1 auto;
    display: flex;
}

@media only screen and (max-width: 800px) {
    #main {
        position: relative;
    }

    .navbar-toggler-icon {
        display: block;
    }

    #home {
        flex-direction: column;
    }
}
